<template>
  <div>
    <el-carousel  class="banner" height="550px" arrow="never" indicator-position="none">
      <el-carousel-item >
        <img src="../../assets/banner7.jpg"/>
      </el-carousel-item>
    </el-carousel>
    <div class="pt60 pb60" >
      <div class="main-container">
        <div class="center fs32" ></div>
        <el-row :gutter="24">
          <el-col  >
            <div class="company-profile">
            <p>2016</p>
            <p>--</p>
            <p></p>
            </div>
          </el-col>
        </el-row>
        <div class="pt60 pb60" >
          <baidu-map class="bm-view" :center="{lng: lng, lat: lat}" :zoom="20" v-if="visible">
            <el-button type="primary" @click="visible=false">x</el-button>
            <bm-marker :position="{lng: lng, lat:lat }"   @click="infoWindowOpen" animation="BMAP_ANIMATION_BOUNCE">
              <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">{{address}}</bm-info-window>
              <bm-label :content="address" :labelStyle="{color: '#ec6c6a', fontSize : '18px'}" :offset="{width: -35, height: 30}"/>
            </bm-marker>
          </baidu-map>
        </div>
        <el-button type="primary" @click="getPosA">C</el-button>
        <el-button type="primary" @click="getPosB">E</el-button>
        <br>
        <br>



<map-dialog :visible="visibleTest"  @close="visibleTest=false">
  <baidu-map class="bm-view" :center="{lng: lng, lat: lat}" :zoom="20" >
    <bm-marker :position="{lng: lng, lat:lat }"   @click="infoWindowOpen" animation="BMAP_ANIMATION_BOUNCE">
      <bm-info-window :show="show" @close="infoWindowClose" @open="infoWindowOpen">{{address}}</bm-info-window>
      <bm-label :content="address" :labelStyle="{color: '#ec6c6a', fontSize : '18px'}" :offset="{width: -35, height: 30}"/>
    </bm-marker>
  </baidu-map>
</map-dialog>

        <el-button type="primary" @click="getPosA2">C2</el-button>
        <el-button type="primary" @click="getPosB2">E2</el-button>
        <br>
        {{txt}}


      </div>
    </div>



  </div>
</template>

<script>

  import MapDialog from '../../compontents/mapdialog/MapDialog'
  export default {
    name: 'About',
    components: {
      MapDialog
    },
    data () {
      return {
        visible:false,
        visibleTest:false,
        show: false,
        txt:'000',
        lng:'113.865658',
        lat:'22.58948',
        address:''
      }
    },
    methods: {
      infoWindowClose () {
        this.show = false
      },
      infoWindowOpen () {
        this.show = true
      },
      getPosA () {
          this.visible = true
        this.txt = 'C'
        this.lng = '113.865577'
        this.lat = '22.588929'
        this.address = 'C'
      },
      getPosB () {
        this.visible = true
        this.txt = 'E'
        this.lng = '113.86529'
        this.lat = '22.589547'
        this.address = 'E'
      },
      getPosA2 () {
        this.visibleTest = true
        this.txt = 'C'
        this.lng ='113.865577'
        this.lat ='22.588929'
        this.address ='C'
      },
      getPosB2 () {
        this.visibleTest = true
        this.txt = 'E'
        this.lng = '113.86529'
        this.lat = '22.589547'
        this.address = 'E'
      }
    }
  }
</script>

<style scoped>
.company-profile>p{
  line-height: 1.8;
  font-size:16px;
  color:#666;
  margin-top:20px;
}

</style>
<style>
  .bm-view {
    width: 100%;
    height: 400px;

    border:1px solid #f0f;
  }
</style>



